import $ from 'jquery';
import Swiper, { Navigation, Pagination } from 'swiper';
import HelperCtrl from '../inc/helper';

Swiper.use([Navigation, Pagination]);

const Helper = new HelperCtrl();

function SwiperCtrl() {
	// this.breadcrumb = this.breadcrumb.bind(this);
	this.init();
}

SwiperCtrl.prototype.init = function() {
  const breadcrumb = new Swiper('.bread-container', this.breadcrumb());
	const catalogCarousel = new Swiper('.catalog-swiper', this.catalogCarousel());
	const staffCarousel = new Swiper('.other-staff-list', this.staffCarousel());
}

SwiperCtrl.prototype.breadcrumb = function() {
	const $breadcrumb = $('.bread-container');
  const leng = $breadcrumb.find('li').length;
  return {
    initialSlide: leng,
    freeMode: true,
		slidesPerView: 'auto',
		spaceBetween: 0,
	}
}

SwiperCtrl.prototype.catalogCarousel = function() {
  return {
		navigation: {
			nextEl: '.catalog-list-next',
			prevEl: '.catalog-list-prev',
		},
		scrollbar: {
			el: '.catalog-scrollbar',
			// hide: false,
		},
		freeMode: true,
		slidesPerView: 'auto',
		centeredSlides: true,
		spaceBetween: 8,
		grabCursor: true,
		speed: 800,
		// autoplay: 4000,
		autoplayDisableOnInteraction: false,
		// mousewheelControl: true,
    breakpoints: {
      768: {
        centeredSlides: false
      }
    }
	}
}

SwiperCtrl.prototype.staffCarousel = function() {

	return {
		// scrollbar: '.cat-tag-scrollbar',
		// scrollbarHide: false,
		// freeMode: true,
		pagination: {
			el: '.staff-list-pagination',
			clickable: true
		},
		navigation: {
			nextEl: '.staff-list-next',
			prevEl: '.staff-list-prev',
		},
		slidesPerView: 'auto',
		centeredSlides: true,
		spaceBetween: 32,
    grabCursor: true,
    breakpoints: {
      768: {
        centeredSlides: false
      }
    }
	}
}

new SwiperCtrl();